<template>
    <div class="container-fluid py-4">
        <h1 class="title-table mb-4">TABLA INSTRUCTOR</h1>
        <div class="table-responsive rounded-3 shadow-sm">
        <table class="table table-hover align-middle">
            <thead class="table-success">
                <tr>
                    <th scope="col" class="ps-4">Codigo</th>
                    <th scope="col" >Nombre</th>
                    <th scope="col" >Apellido</th>
                    <th scope="col" >Edad</th>
                    <th scope="col" >Fecha Nacimiento</th>
                    <th scope="col" >Direccion</th>
                    <th scope="col" >Telefono</th>
                    <th scope="col" class="pe-4">Asignatura</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="instructor in instructores" :key="instructor.codigo"  class="table-row">
                    <td class="ps-4 fw-medium">{{instructor.codigo}}</td>
                    <td>{{instructor.nombre}}</td>
                    <td>{{instructor.apellido}}</td>
                    <td><span class="badge bg-success bg-opacity-10 text-success">{{instructor.edad}} años</span></td>
                    <td>{{instructor.fechanac}}</td>
                    <td>{{instructor.direccion}}</td>
                    <td>  <i class="bi bi-telephone me-2"></i>{{instructor.tel}}</td>
                    <td class="pe-4"><span class="badge bg-success bg-opacity-10 text-success">{{instructor.asignatura }} </span></td>
                </tr>
            </tbody>
        </table>

    </div>
</div>
</template>

<script>
import axios from 'axios';
export default{
    data(){
        return{
            instructores:[],
        };
    },
    methods:{
        obtenerInstructor(){
            axios
            .get("http://localhost:8081/api/instructor/listar")
            .then((response)=>{
                this.instructores=response.data;

            })
            .catch((error)=>{
                console.error("error al listar a los aprendices",error);

            });
        },

    },
    mounted(){
        this.obtenerInstructor();
    },
};
</script>

<style scoped>
.title-table {
    color: #38A900;
    font-size: 1.8rem;
    font-weight: 700;
    letter-spacing: -0.5px;
}

.table-success {
    --bs-table-bg: #38A900;
    --bs-table-color: white;
    font-weight: 500;
}

.table-hover tbody tr:hover {
    background-color: rgba(56, 169, 0, 0.03);
    transform: translateX(4px);
    transition: all 0.2s ease;
}

.table-row {
    border-bottom: 1px solid rgba(0,0,0,0.05);
}

.badge {
    padding: 0.35em 0.65em;
    font-size: 0.85em;
    border-radius: 0.5rem;
    white-space: normal;
}

.bi-telephone {
    color: #38A900;
    font-size: 1.1rem;
}
</style>